import React, {ChangeEvent, Component} from 'react';
import {FilterType, Sort_By_type} from '../../App';

type SelectPropsType = {
    changeFilters: (value: Sort_By_type, name: string) => void;
    filters: FilterType
}

type DefaultProps = {
    options: Array<{ label: string, value: string }>
}

class Select extends Component<SelectPropsType, {}> {
    static defaultProps: DefaultProps = {
        options: [
            {label: '  ', value: 'popularity.desc'},
            {label: '  ', value: 'popularity.asc'},
            {label: '  ', value: 'vote_average.desc'},
            {label: '  ', value: 'vote_average.asc'},
        ]
    }

    shouldComponentUpdate(nextProps: Readonly<SelectPropsType>, nextState: Readonly<{}>, nextContext: any): boolean {
        if (this.props.filters.sort_by !== nextProps.filters.sort_by) {
            return true
        } else return false;
    }

    selectValue = (e: ChangeEvent<HTMLSelectElement>) => {
        this.props.changeFilters(e.target.value as Sort_By_type, e.target.name);
    }

    render() {
        const {filters} = this.props;
        return <div className="form-group">
            <label htmlFor="sort_by"> :</label>
            <select className="form-control" id="sort_by" name="sort_by" onChange={this.selectValue}
                // defaultValue={filters.sort_by}
                    value={filters.sort_by}
            >
                {
                    Select.defaultProps.options.map(option => <option value={option.value}
                                                                      key={option.value}>{option.label}</option>)
                }

            </select>
        </div>
    }
}

export default Select;